import React, { Component } from 'react';
import Slider from 'react-slick';
import './index.css';

const PortfolioElement = ({ title, imageClass }) => {
	return (
		<div className="portfolio-element">
			<div className="portfolio-element__title">{title}</div>
			<div className="portfolio-element__image">
				<img src={require(`../../images/portfolio/${imageClass}`)} alt="" />
			</div>
		</div>
	);
};

class Portfolio extends Component {
	render() {
		const settings = {
			arrows: true,
			dots: false,
			infinite: true,
			speed: 500,
			slidesToShow: 1,
			slidesToScroll: 1,
			centerMode: true,
			centerPadding: '10px',
		};

		return (
			<div className="portfolio">
				<div className="portfolio__slider">
					<Slider {...settings} ref={slider => (this.slider = slider)}>
						<div>
							<PortfolioElement
								title="Certificate from Yandex"
								imageClass="yandex-certificate.jpg"
							/>
						</div>
						<div>
							<PortfolioElement
								title="Certificate from Yandex"
								imageClass="yandex.jpg"
							/>
						</div>
					</Slider>
				</div>
			</div>
		);
	}
}

export default Portfolio;



