import React, { Component } from 'react';
import CustomScroll from 'react-custom-scroll';
import PersonalInfo from './components/PersonalInfo/';
import Section from './components/Section/';
import Skills from './components/Skills/';
import Portfolio from './components/Portfolio/';

class App extends Component {
	render() {
		return (
			<div className="App">
				<PersonalInfo />
				<Section
					header={{ text: 'Skills', color: '#fff' }}
					subheader="I design in the browser with HTML(5), CSS(3) and a touch of JavaScript. I love coding things from scratch, but I can work with front-end frameworks"
				>
					<Skills />
				</Section>
				<Section header={{ text: 'Portfolio', color: '#fff' }}>
					<Portfolio />
				</Section>
			</div>
		);
	}
}

export default App;



